@-webkit-keyframes floatUp{
    0%{top: 100vh; opacity: 0;}
    25%{opacity: 1;}
    50%{top: 0vh; opacity: .8;}
    75%{opacity: 1;}
    100%{top: -100vh; opacity: 0;} 
  }
@-moz-keyframes floatUp{
  0%{top: 100vh; opacity: 0;}
  25%{opacity: 1;}
  50%{top: 0vh; opacity: .8;}
  75%{opacity: 1;}
  100%{top: -100vh; opacity: 0;} 
}
@-o-keyframes floatUp{
  0%{top: 100vh; opacity: 0;}
  25%{opacity: 1;}
  50%{top: 0vh; opacity: .8;}
  75%{opacity: 1;}
  100%{top: -100vh; opacity: 0;} 
}
@keyframes floatUp{
  0%{top: 100vh; opacity: 0;}
  25%{opacity: 1;}
  50%{top: 0vh; opacity: .8;}
  75%{opacity: 1;}
  100%{top: -100vh; opacity: 0;} 
}


.contain-box{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1200px;
  flex-wrap: wrap;
  z-index: 1;
}

.contain-box .card{
  position: relative;
  width: 280px;
  height: 400px;
  margin: 30px;
  box-shadow: -10px -1px 50px rgba(0,0,0, 0.5);
  border-radius: 15px;
  background: rgba(255,255,255, 0.1);
  overflow: hidden;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid rgba(255,255,255, 0.5);
  border-right: 1px solid rgba(255,255,255, 0.5);
  backdrop-filter: blur(5px);
}

.contain-box .card .content-profile{
  padding: 20px;
  text-align: center;
  transition: 0.5s;
  transform: translateY(100px);
  opacity: 0;
}

.contain-box .card:hover .content-profile{
  transform: translateY(0px);
  opacity: 1;
}

.contain-box .card .img-profile-box{
  padding: 20px;
  text-align: center;
  transition: 0.5s;
}
